import React, { useEffect, useMemo, useRef, useState } from 'react';
import Carousel from 'react-multi-carousel';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from 'store';
import { change } from 'store/directionSlice';
import {
  mediaMaxScreenPC,
  mediaMinScreenMobile,
  mediaScreenMobile,
  mediaScreenTablet,
} from 'utils/constants';
import {
  BuyContent1,
  BuyContentStep1,
  BuyContentStep2,
  BuyContentStep3,
} from '../Direction';
import Tab2, { PropsTab2ContentSection } from '../Tabs2';

type PropsModalBody = {
  id: number;
  onTabChange?: (id: number) => void;
};

const responsive = {
  desktop: {
    breakpoint: { max: mediaMaxScreenPC, min: mediaScreenTablet },
    items: 1,
  },
  tablet: {
    breakpoint: { max: mediaScreenTablet, min: mediaScreenMobile },
    items: 1,
  },
  mobile: {
    breakpoint: { max: mediaScreenMobile, min: mediaMinScreenMobile },
    items: 1,
  },
};

const ModalBody1 = ({ id, onTabChange = () => {} }: PropsModalBody) => {
  const dispatch = useDispatch();
  const carousel = useRef<any>();
  const { activeImgUrl } = useSelector((state: RootState) => state.direction);
  const [activeTabs, setActiveTabs] = useState<{ [key: string]: number }>({});

  const tabs = useMemo(() => {
    let data: PropsTab2ContentSection[] = [];
    switch (id) {
      case BuyContent1[0].id:
        data = BuyContentStep1;
        break;
      case BuyContent1[1].id:
        data = BuyContentStep2;
        break;
      default:
        data = BuyContentStep3;
        break;
    }

    return data;
  }, [id]);

  useEffect(() => {
    if (carousel.current) {
      const index = BuyContent1.findIndex(b => b.id === id);

      if (index === -1) return;

      carousel.current?.goToSlide(index);
    }
  }, [id]);

  return (
    <div className="directtion-mobile__body">
      <Carousel
        ref={carousel}
        responsive={responsive}
        renderArrowsWhenDisabled
        beforeChange={nextSlide => {
          setActiveTabs({});
          onTabChange(BuyContent1[nextSlide].id);
          switch (nextSlide) {
            case 0:
              dispatch(change(BuyContentStep1[0].img));
              break;
            case 1:
              dispatch(change(BuyContentStep2[0].img));
              break;
            default:
              dispatch(change(BuyContentStep3[0].img));
              break;
          }
        }}
      >
        {BuyContent1.map(b => (
          <React.Fragment key={b.id}>
            <div className="directtion-mobile__img">
              <img className="cover-img" src={activeImgUrl} alt="image" />
            </div>
            <Tab2
              tabs={tabs}
              defaultActive={tabs[0].id}
              className="directtion-mobile__tab2"
              active={activeTabs[b.id] || tabs[0].id}
              onChangeActive={subId =>
                setActiveTabs(old => {
                  old[b.id] = subId;
                  return old;
                })
              }
            />
          </React.Fragment>
        ))}
      </Carousel>
    </div>
  );
};

export default ModalBody1;
