import { useMemo } from 'react';
import { BuyContent2 } from '../Direction';
import Tab2 from '../Tabs2';

type PropsModalBody = {
  id: number;
  onTabChange?: (id: number) => void;
};

const ModalBody2 = ({ id, onTabChange = () => {} }: PropsModalBody) => {
  const img = useMemo(() => {
    const index = BuyContent2.findIndex(b => b.id === id);
    return index !== -1 ? BuyContent2[index].img : '';
  }, [id]);

  return (
    <>
      <div className="directtion-mobile__img">
        <img className="cover-img" src={img} alt="image" />
      </div>
      <Tab2
        active={id}
        tabs={BuyContent2}
        className="directtion-mobile__tab2"
        onChangeActive={onTabChange}
      />
    </>
  );
};

export default ModalBody2;
