import cx from 'classnames';
import React, { ReactElement, useState } from 'react';
import { useTranslation } from 'react-i18next';

export type PropsTab3ContentSection = {
  id: number;
  title: ReactElement;
  content: ReactElement;
};

type PropsTab3Section = {
  title: string;
  defaultActive?: number;
  tabs:
    | PropsTab3ContentSection[]
    | ((active: boolean) => PropsTab3ContentSection[]);
} & React.HTMLAttributes<HTMLDivElement>;

const Tab3 = ({
  title,
  className,
  tabs = [],
  defaultActive = 0,
}: PropsTab3Section) => {
  const { t } = useTranslation();
  const [activeTab, setActiveTab] = useState(defaultActive);

  const handleActiveTab = (id: number) => {
    setActiveTab(id);
  };

  return (
    <div className={cx(['tab3', className])}>
      {title ? (
        <h3 className="container tab3__title color-grey-1000">{t(title)}</h3>
      ) : null}
      <ul className="tab3__controller">
        {(typeof tabs === 'function'
          ? tabs(activeTab === defaultActive)
          : tabs
        ).map(ta => (
          <li
            key={ta.id}
            className={cx({
              tab3__item: true,
              active: activeTab === ta.id,
            })}
            onClick={() => handleActiveTab(ta.id)}
          >
            {ta.title}
          </li>
        ))}
      </ul>
      <div className="tab3__content">
        {(typeof tabs === 'function' ? tabs(activeTab === 0) : tabs).map(ta => (
          <React.Fragment key={ta.id}>
            {activeTab === ta.id ? ta.content : null}
          </React.Fragment>
        ))}
      </div>
    </div>
  );
};

export default Tab3;
