import { Modal } from 'antd';
import { Icon } from 'assets/svg';
import { useMemo, useState } from 'react';
import { Trans } from 'react-i18next';
import { BuyContent1, BuyContent2 } from '../Direction';
import ModalBody1 from '../ModalBody1';
import ModalBody2 from '../ModalBody2';
import Tab3, { PropsTab3ContentSection } from '../Tabs3';

const Content1 = ({ defaultActive = 1 }: { defaultActive?: number }) => {
  const [activeTab, setActiveTab] = useState(defaultActive);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const title = useMemo(() => {
    return BuyContent1.find(b => b.id === activeTab)?.title || '';
  }, [activeTab]);

  const handleOpenModel = (id: number) => {
    setActiveTab(id);
    setIsModalOpen(true);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  return (
    <div className="directtion-mobile__container">
      <ul className="directtion-mobile__step">
        {BuyContent1.map((b, i) => (
          <li key={b.id} onClick={() => handleOpenModel(b.id)}>
            <div>
              <p>
                <Trans i18nKey="Bước"></Trans> {i + 1}
              </p>
              <p>{b.title}</p>
            </div>
            <Icon.RightArrow />
          </li>
        ))}
      </ul>

      <Modal
        title={title}
        width={'100%'}
        open={isModalOpen}
        footer={<div></div>}
        onCancel={handleCancel}
      >
        <ModalBody1 id={activeTab} onTabChange={setActiveTab} />
      </Modal>
    </div>
  );
};

const Content2 = ({ defaultActive = 1 }: { defaultActive?: number }) => {
  const [activeTab, setActiveTab] = useState(defaultActive);
  const [isModalOpen, setIsModalOpen] = useState(false);

  const title = useMemo(() => {
    const index = BuyContent2.findIndex(b => b.id === activeTab);
    return index !== -1 ? BuyContent2[index].title : '';
  }, [activeTab]);

  const handleOpenModel = (id: number) => {
    setActiveTab(id);
    setIsModalOpen(true);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  return (
    <div className="directtion-mobile__container">
      <ul className="directtion-mobile__step">
        {BuyContent2.map((b, i) => (
          <li key={b.id} onClick={() => handleOpenModel(b.id)}>
            <div>
              <p>
                <Trans i18nKey="Bước"></Trans> {i + 1}
              </p>
              <p>{b.title}</p>
            </div>
            <Icon.RightArrow />
          </li>
        ))}
      </ul>

      <Modal
        title={title}
        width={'100%'}
        open={isModalOpen}
        footer={<div></div>}
        onCancel={handleCancel}
      >
        <ModalBody2 id={activeTab} onTabChange={setActiveTab} />
      </Modal>
    </div>
  );
};

const tabs: (isActiveFirst: boolean) => PropsTab3ContentSection[] = active => [
  {
    id: 1,
    title: (
      <>
        {active ? (
          <Icon.Card className="parent" />
        ) : (
          <Icon.CardOutline className="parent" />
        )}
        <span>
          <Trans i18nKey="direction.Hướng dẫn mua hàng"></Trans>
        </span>
      </>
    ),
    content: <Content1 />,
  },
  {
    id: 2,
    title: (
      <>
        {!active ? (
          <Icon.Pay className="parent" />
        ) : (
          <Icon.PayOutline className="parent" />
        )}
        <span>
          <Trans i18nKey="direction.Thanh toán các kỳ"></Trans>
        </span>
      </>
    ),
    content: <Content2 />,
  },
];

const DirectionMobile = () => {
  return (
    <Tab3
      tabs={tabs}
      className="directtion-mobile"
      defaultActive={tabs(true)[0].id}
      title="direction.Hướng dẫn thanh toán"
    />
  );
};

export default DirectionMobile;
