import Carousel from 'react-multi-carousel';
import {
  mediaMaxScreenPC,
  mediaMinScreenMobile,
  mediaScreenMobile,
  mediaScreenTablet,
} from 'utils/constants';
import { carouselsData } from './const';

const responsive = {
  desktop: {
    breakpoint: { max: mediaMaxScreenPC, min: mediaScreenTablet },
    items: 6,
  },
  tablet: {
    breakpoint: { max: mediaScreenTablet, min: mediaScreenMobile },
    items: 6, //6.85,
  },
  mobile: {
    breakpoint: { max: mediaScreenMobile, min: mediaMinScreenMobile },
    items: 3, //3.05,
  },
};

const Carousel1 = () => {
  return (
    <div className="carousel1">
      <Carousel
        arrows={false}
        autoPlay={true}
        infinite={true}
        autoPlaySpeed={5000}
        responsive={responsive}
        itemClass="carousel1__item"
      >
        {carouselsData.map(c => (
          <div key={c.id}>
            <img src={c.url} alt={c.alt} />
          </div>
        ))}
      </Carousel>
    </div>
  );
};

export default Carousel1;
