import { Icon } from 'assets/svg';
import cx from 'classnames';
import { ReactElement, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import { change } from 'store/directionSlice';

export type PropsTab2ContentSection = {
  id: number;
  img: string;
  title: ReactElement;
  content: ReactElement;
};

type PropsTab2Section = {
  active?: number;
  defaultActive?: number;
  tabs: PropsTab2ContentSection[];
  onChangeActive?: (id: number) => void;
} & React.HTMLAttributes<HTMLDivElement>;

const Tab2 = ({
  active,
  className,
  tabs = [],
  defaultActive = 1,
  onChangeActive = () => {},
}: PropsTab2Section) => {
  const [activeTab, setActiveTab] = useState(defaultActive);
  const dispatch = useDispatch();

  const handleActiveTab = (id: number) => {
    setActiveTab(id);
    onChangeActive(id);
    const index = tabs.findIndex(t => t.id === id);

    if (index === -1) return;

    dispatch(change(tabs[index].img));
  };

  const renderIndex = (i: number, act: boolean) => {
    switch (i) {
      case 0:
        return act ? <Icon.Step1 /> : <Icon.Step1Outline />;
      case 1:
        return act ? <Icon.Step2 /> : <Icon.Step2Outline />;
      default:
        return act ? <Icon.Step3 /> : <Icon.Step3Outline />;
    }
  };

  useEffect(() => {
    if (active) {
      setActiveTab(active);
    }
  }, [active]);

  return (
    <div className={cx(['tab2', className])}>
      <div className="tab2__content">
        {tabs.map((ta, i) => (
          <div
            key={ta.id}
            className="tab2__item"
            onClick={() => handleActiveTab(ta.id)}
          >
            <div className="tab2__index">
              {renderIndex(i, activeTab === ta.id)}
            </div>
            <h4 className="tab2__title pointer">{ta.title}</h4>
            {activeTab === ta.id ? (
              <div className="tab2__text">{ta.content}</div>
            ) : null}
          </div>
        ))}
      </div>
    </div>
  );
};

export default Tab2;
